:root {
  --bg: #1a001f;
  --primary: #8a2be2;
  --secondary: #2c003e;
  --light: #eee;
  --dark: #121212;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  background: var(--bg);
  color: var(--light);
}
main {
  flex: 1;
  padding: 40px 20px;
}

a {
  color: var(--primary);
  text-decoration: none;
  transition: color .3s;
}
a:hover {
  color: var(--light);
}

/* Header */
header {
  position: relative;
  z-index: 102;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--secondary);
  padding: 20px 40px;
}
.header-brand {
  display: flex;
  align-items: center;
  gap: 10px;
}
.header-brand img {
  width: 60px;
}
.logo-text {
  color: var(--primary);
  font-size: 1.8rem;
}

/* Navegación escritorio */
nav.desktop-nav {
  display: block;
}
nav.desktop-nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}
nav.desktop-nav a {
  font-weight: 500;
}
nav.desktop-nav a:hover {
  color: var(--light);
}

/* Icono hamburguesa */
.menu-icon {
  display: none;
  font-size: 1.5rem;
  color: var(--light);
  cursor: pointer;
}

/* Overlay */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 100;
}

/* Sidebar móvil */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 250px;
  height: 100%;
  background: var(--secondary);
  transform: translateX(-100%);
  transition: transform .3s ease-in-out;
  z-index: 101;
  padding-top: 80px;
}
.sidebar.open {
  transform: translateX(0);
}
.sidebar ul {
  list-style: none;
  padding: 0;
}
.sidebar li {
  margin: 20px 0;
  padding: 0 20px;
}
.sidebar a {
  color: var(--light);
  font-size: 1.2rem;
}
.sidebar a:hover {
  color: var(--primary);
}

/* Hero */
.hero {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: linear-gradient(135deg, var(--secondary) 0%, var(--bg) 100%);
}
.hero h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}
.hero p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  max-width: 600px;
}
.btn {
  background: var(--primary);
  color: var(--dark);
  padding: 12px 24px;
  border-radius: 25px;
  font-weight: 500;
  transition: background .3s;
}
.btn:hover {
  background: #a84aff;
}

/* Servicios */
section h2 {
  font-size: 2.5rem;
  border-bottom: 3px solid var(--primary);
  display: inline-block;
  margin-bottom: 30px;
}
.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
}
.card {
  background: var(--secondary);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
  transition: transform .3s;
}
.card:hover {
  transform: scale(1.05);
}
.card h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: var(--primary);
}
.card p {
  line-height: 1.5;
}

/* Contacto */
.contact-section {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
}
.contact-info {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 15px;
  font-size: 1.1rem;
  margin: 20px 0;
}
.social-list {
  list-style: none;
  display: flex;
  gap: 30px;
  justify-content: center;
  margin-top: 20px;
}
.social-list li a {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--light);
  font-size: 1rem;
}
.social-list li a:hover {
  color: var(--primary);
}

/* Footer */
footer {
  background: var(--dark);
  color: var(--light);
  padding: 60px 20px;
  border-top: 4px solid var(--primary);
}
.footer-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}
.footer-section {
  flex: 1;
  min-width: 200px;
  word-break: break-word;
}
.footer-section h3 {
  font-size: 1.3rem;
  color: var(--primary);
  margin-bottom: 15px;
}
.footer-section p,
.footer-section a {
  line-height: 1.6;
}
.footer-section ul {
  list-style: none;
  padding: 0;
}
.footer-section ul li {
  margin-bottom: 10px;
}
.footer-social {
  display: flex;
  gap: 15px;
  margin-top: 10px;
}
.footer-social a {
  display: inline-flex;
  align-items: center;
  gap: 8px;
}
.footer-social a svg {
  width: 20px;
  height: 20px;
}
.footer-social a:hover {
  color: var(--primary);
}
.footer-bottom {
  text-align: center;
  margin-top: 30px;
  border-top: 1px solid #444;
  padding-top: 20px;
  font-size: 0.9rem;
}

/* MEDIA QUERIES */
@media (max-width: 768px) {
  nav.desktop-nav {
    display: none;
  }
  .menu-icon {
    display: block;
  }
  header {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 10px;
  }
  .services-grid {
    grid-template-columns: 1fr;
  }
  .footer-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 30px;
  }
  .social-list {
    flex-direction: column;
    gap: 15px;
  }
}

@media (max-width: 480px) {
  .header-brand img {
    width: 50px;
  }
  .logo-text {
    font-size: 1.5rem;
  }
  nav a {
    font-size: 0.9rem;
  }
  .hero h1 {
    font-size: 2rem;
  }
  .hero p {
    font-size: 0.9rem;
  }
  .btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
  section h2 {
    font-size: 2rem;
  }
}
